@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,700,800);
.header {
  color: #ffffff;
  text-align: center;
  font-size: 3rem;
  padding: 1rem;
}

@media screen and (max-width: 768px) {
  .header {
    font-size: 1.6rem;
  }
}

.movie_listings {
  display: flex;
  flex-wrap: wrap;
  margin: 0 4rem;
}
.movie_card {
  position: relative;
  display: block;
  width: 666px;
  height: 350px;
  margin: 2rem auto;
  overflow: hidden;
  border-radius: 10px;
}

#bright {
  box-shadow: 0px 0px 150px -45px rgba(255, 51, 0, 0.5);
}
#bright:hover {
  box-shadow: 0px 0px 120px -55px rgba(255, 51, 0, 0.5);
}

.movie_card .info_section {
  position: relative;
  width: 100%;
  height: 100%;
  background-blend-mode: multiply;
  z-index: 2;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: self-start;
  align-content: flex-start;
}
.movie_card .info_section .movie_header {
  position: relative;
  padding: 10px;
  width: 100%;
}
.movie_card .info_section .movie_header h1 {
  color: #fff;
  font-weight: 400;
}
.movie_card .info_section .movie_header h4 {
  color: #9ac7fa;
  font-weight: 400;
}
.movie_card .info_section .movie_header .minutes {
  display: inline-block;
  margin-top: 10px;
  color: #fff;
  padding: 5px;
  border-radius: 5px;
  border: 1px solid rgba(255, 255, 255, 0.13);
}

.movie_card .info_section .movie_header .minutes:nth-of-type(2) {
  margin-left: 4px;
}

.movie_card .info_section .movie_header .type {
  color: #cee4fd;
  margin-left: 100px;
  margin-top: 10px;
  font-size: 1.2rem;
}
.movie_card .info_section .movie_header .locandina {
  position: relative;
  float: left;
  margin-right: 20px;
  height: 180px;
  box-shadow: 0 0 20px -10px rgba(0, 0, 0, 0.5);
}
.movie_card .info_section .movie_desc {
  padding: 10px;
  width: 100%;
}
.movie_card .info_section .movie_desc .text {
  color: #cfd6e1;
}
.movie_card .blur_back {
  position: absolute;
  top: 0;
  z-index: 1;
  height: 100%;
  right: 0;
  background-size: cover !important;
  border-radius: 11px;
  background-repeat: no-repeat !important;
}

.read_more a {
  text-decoration: none;
  background: white;
  padding: 1rem 4rem;
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  color: #333;
  cursor: pointer;
  font-size: 1.6rem;
  margin-top: 50px;
  display: block;
  color: darkred;
  transition: all 0.2s ease-in-out;
}

.read_more a:hover,
.read_more a:focus {
  color: white;
  background-color: darkred;
}

@media screen and (min-width: 768px) {
  .movie_header {
    width: 60%;
  }

  .movie_desc {
    width: 50%;
  }

  .info_section {
    background: linear-gradient(to right, #0d0d0c 50%, transparent 100%);
  }

  .blur_back {
    width: 80%;
    background-position: -100% 10% !important;
  }
}
@media screen and (max-width: 768px) {
  .movie_listings {
    margin: 0 1rem;
  }

  .movie_card {
    width: 95%;
    margin: 20px auto;
    min-height: 350px;
    height: auto;
  }

  .locandina {
    height: 300px !important;
    padding: 1rem;
    border-radius: 20px;
    margin: 0 !important;
    float: none !important;
  }

  .blur_back {
    width: 100%;
    background-position: 50% 50% !important;
  }

  .movie_header {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .movie_header h1 {
    font-size: 1.2rem;
    text-align: center;
    margin-bottom: 1rem;
  }

  .movie_card .info_section .movie_header .minutes {
    width: 50%;
    text-align: center;
  }

  .movie_card .info_section .movie_header .minutes:nth-of-type(2) {
    margin: 0.4rem;
  }

  .movie_card .info_section .movie_header .type {
    margin-left: 0;
    font-size: 1rem;
  }

  .movie_card .info_section .movie_desc .text {
    text-align: center;
  }

  .movie_desc {
    width: 100%;
  }

  .info_section {
    background: linear-gradient(to top, #141413 50%, transparent 100%);
    display: inline-grid;
  }

  .read_more a {
    padding: 1rem 5rem;
    font-size: 1rem;
    margin-top: 0;
  }
}

@media screen and (min-width: 2560px) {
  .movie_card {
    flex: 1 0 20%;
    margin: 1rem;
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body {
  margin: 0;
  background: black;
  font-family: "Montserrat", helvetica, arial, sans-serif;
  font-size: 14px;
  font-weight: 400;
}

